<template>
    <div>
        <top-nav title="订单列表"></top-nav>
        <!--<div class="wyu-tips wyu-tips-warning">-->
            <!--<p>1).拼团订单需要 拼团成功</p>-->
        <!--</div>-->
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form :inline="true" :model="search" class="search-form" size="mini">
                    <el-form-item label="用户ID">
                        <el-input type="text" v-model="search.user_id"/>
                    </el-form-item>
                    <el-form-item label="账号">
                        <el-input type="text" v-model="search.mobile"/>
                    </el-form-item>

                    <el-form-item>
                        <el-button size="mini" type="primary" @click="getOrderList(1)" icon="el-icon-search">搜索</el-button>
                    </el-form-item>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" >
                    <el-table-column width="80" prop="order_id" label="订单ID"></el-table-column>

                    <el-table-column prop="order_sn" label="订单号" ></el-table-column>

                    <el-table-column label="用户ID" >
                        <template slot-scope="scope" >
                            <p>{{scope.row.user_id}} |
                                <router-link  :to="'/customer/view/' + scope.row.user_id" target="_blank" ><el-button type="text">跟踪</el-button></router-link>
                            </p>
                        </template>
                    </el-table-column>

                    <el-table-column label="用户信息">
                        <template slot-scope="scope">
                            <p>{{scope.row.nickname}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column prop="all_money" label="价格"></el-table-column>

                    <el-table-column prop="pay_money" label="在线支付"></el-table-column>

                    <el-table-column prop="user_money" label="余额支付"></el-table-column>

                    <el-table-column prop="pay_type_mean" label="支付方式"></el-table-column>

                    <el-table-column prop="type_mean" label="开通方式"></el-table-column>

                    <el-table-column prop="add_time" label="下单时间" width="180"></el-table-column>

                    <el-table-column prop="add_ip" label="下单时间" width="180"></el-table-column>

                    <el-table-column label="操作" width="180">
                        <template slot-scope="scope">
                            <el-button type="text" @click="dialog=true,order_id=scope.row.order_id,is_distribut=scope.row.is_distribut">{{scope.row.is_distribut == 1 ? '查看分佣' : '分佣'}}</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div style="text-align: right" class="block">
                    <el-pagination @current-change="getOrderList" :page-size="datas.limit"
                                   layout="total, prev, pager, next" :total="datas.count">
                    </el-pagination>
                </div>
            </div>
        </div>

        <!--开通会员分佣-->
        <el-dialog title="开通会员分佣" :visible.sync="dialog" width="800px" center :modal-append-to-body="false">
            <el-form label-width="100px" style="padding: 0" v-loading="dialogLoading">
                <el-table v-if="fenyong.is_commission" :data="fenyong.list" style="width: 100%" >
                    <el-table-column width="80" prop="level" label="层级"></el-table-column>

                    <el-table-column width="140" prop="now_agent_group" label="顾问等级"></el-table-column>

                    <el-table-column width="80" label="用户ID">
                        <template slot-scope="scope" >
                            <p>{{scope.row.user_id}} |
                                <router-link  :to="'/agent/manage/view/' + scope.row.user_id" target="_blank" ><el-button type="text">跟踪</el-button></router-link>
                            </p>
                        </template>
                    </el-table-column>

                    <el-table-column prop="nickname" label="昵称" ></el-table-column>

                    <el-table-column label="头像" >
                        <template slot-scope="scope">
                            <img :src="scope.row.face" width="50px"/>
                        </template>
                    </el-table-column>

                    <el-table-column prop="mobile" label="联系方式"></el-table-column>

                    <el-table-column label="佣金">
                        <template slot-scope="scope">
                            ￥ {{scope.row.money}}
                        </template>
                    </el-table-column>
                </el-table>
                <el-form-item>
                    <el-button size="mini" @click="dialog = false;dialogLoading = false;">取 消</el-button>
                    <el-button size="mini" type="primary" v-if="is_distribut == 0" @click="handlefenyong(1)">确定分佣</el-button>
                    <el-button size="mini" type="primary" v-if="is_distribut == 0" @click="handlefenyong(0)">拒绝分佣</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

    </div>
</template>
<script>
    import * as api from '@/api.js';
    import TopNav from "@/components/TopNav";
    import DateBetween from "../../../components/DateBetween";
    export default {
        name: "customerIndex",
        components: {DateBetween, TopNav},
        data: function () {
            return {
                //整体菊花
                select:1,
                loading: false,
                dialogLoading: false,
                dialog: false,
                search:{
                    date:[],
                    user_id:'',
                    mobile: '',
                },
                status:{},
                datas:{},
                userList:[],
                order_id: 0,
                fenyong: [],
                is_distribut: 1,
            };
        },
        created() {
            this.getOrderList();
        },

        watch:{
            dialog(dialog){//普通的watch监听
                if (dialog == true) {
                    this.getfenyong();
                }
            },
        },


        methods: {


            //获取订单列表
            getOrderList(page=1){
                this.loading = true;
                api.post("/shop/admin.order.userorder/index", {
                    appToken: this.$store.getters.appToken,
                    page:page,
                    end_add_time:this.search.date[1] ? this.search.date[1] :  0,
                    bg_add_time:this.search.date[0] ? this.search.date[0] : 0,
                    keywords:this.search.keywords,
                    keywords_type:this.search.keywords_type,
                    pay_status:this.search.pay_status,
                    pay_type:this.search.pay_status,
                    shipping_status:this.search.shipping_status,
                    order_status:this.search.order_status,
                    prom_type:this.search.prom_type,
                    device:this.search.device,
                }).then(res => {
                    this.loading = false;
                    this.datas = res;
                    if(page <= 1){
                        this.status = res.status;
                    }
                }).catch(() => {
                    this.loading = false;
                });
            },


            getfenyong() {
                if (this.order_id == 0) {
                    this.$message({
                        message: '请选择一个订单',
                        type: 'error',
                    });
                    return false;
                }
                this.dialogLoading = true;
                api.post("/shop/admin.order.userorder/getCommission", {
                    appToken: this.$store.getters.appToken,
                    order_id: this.order_id,
                }).then(res => {
                    this.fenyong = res;
                    this.dialogLoading = false;
                }).catch(() => {
                    this.is_distribut = 1;
                    this.dialogLoading = false;
                });
            },


            handlefenyong(is_fenyong) {
                if (this.order_id == 0) {
                    this.$message({
                        message: '请选择一个订单',
                        type: 'error',
                    });
                    return false;
                }
                this.dialogLoading = true;
                api.post("/shop/admin.order.userorder/Commission", {
                    appToken: this.$store.getters.appToken,
                    order_id: this.order_id,
                    is_fenyong: is_fenyong,
                }).then(res => {
                    this.$message({
                        message: '操作成功！',
                        type: 'success',
                    });
                    this.dialog = false;
                    this.dialogLoading = false;
                }).catch(() => {
                    this.dialogLoading = false;
                });
            }
        }
    };
</script>
